<template v-if='true'>
    <div class="row ms-4 align-items-center">
      <div class='col-md-6'>
        <div class='row'>
          <div class='table-responsive'>
          <table class="table table-striped table-responsive overflow-auto">
               <thead>
                  <tr>
                      <th scope="col">Nmero ticket</th>
                      <th scope="col">Hora</th>
                      <th scope="col">Forma de pago</th>
                      <th scope="col">Total ({{total}} )</th>
                  </tr>
              </thead>
              <tbody>
                  <tr v-for="(item, index) of listaTickets" v-bind:key={index}
                  v-bind:class="{estiloActivoTicketCaja: index === activo}"
                  @click='setTicketActivo(index)'>
                      <td style='text-align: center'>{{item._id}}</td>
                      <td>
                        {{item.timestamp}}
                        </td>
                      <td>{{item.tipoPago}}</td>
                      <td>{{item.total.toFixed(2)}} </td>
                  </tr>
              </tbody>
          </table>
          </div>
        </div>
        <br />
        <div class='row ms-5'>
          <div class='col'>
            <button type="button" class="btn btn-primary btn-block botonesWidth"
            data-bs-toggle="modal" data-bs-target="#vueModalClausura">
              <i class="bi bi-lock-fill iconosBootstrap"></i>
            </button>
          </div>
          <div class='col'>
            <button type="button" class="btn btn-secondary btn-block botonesWidth"
            data-bs-toggle="modal" data-bs-target="#vueModalApertura">
              <i class="bi bi-unlock-fill iconosBootstrap"></i>
            </button>
          </div>
          <div class='col'>
            <button type="button" class="btn btn-danger btn-block botonesWidth"
            data-bs-toggle="modal" data-bs-target="#modalSalidaDinero">
              <i class="bi bi-box-arrow-right iconosBootstrap"></i>
            </button>
          </div>
          <div class='col'>
            <button type="button" class="btn btn-info btn-block botonesWidth">
              <i class="bi bi-printer-fill iconosBootstrap"></i>
            </button>
          </div>
          <div class='col'>
            <button type="button" class="btn btn-success btn-block botonesWidth"
            data-bs-toggle="modal" data-bs-target="#modalEntradaDinero">
              <i class="bi bi-box-arrow-in-right iconosBootstrap"></i>
            </button>
          </div>
        </div>
      </div>
      <div class='col'>
        <DetalleTicket :ticket='ticketInfo' />
      </div>
    </div>
    <AbrirCaja />
    <CerrarCaja />
    <SalidaDinero />
    <EntradaDinero />
</template>

<script>
import { useStore } from 'vuex';
import { computed, ref, onMounted } from 'vue';
import DetalleTicket from '@/components/DetalleTicket.vue';
import AbrirCaja from '@/components/AbrirCaja.vue';
import CerrarCaja from '@/components/CerrarCaja.vue';
import SalidaDinero from '@/components/SalidaDinero.vue';
import EntradaDinero from '@/components/EntradaDinero.vue';
import axios from 'axios';
import moment from 'moment';

export default {
  name: 'Caja',
  setup() {
    const store = useStore();
    const activo = computed(() => store.state.Ticket.activo);
    const total = ref(0);

    /* {
        _id: 1,
        timestamp: '17-09-2021 10:00:00.000',
        tipoPago: 'EFECTIVO',
        total: 10.155,
      } */
    const listaTickets = ref([]);
    // eslint-disable-next-line no-unused-vars
    const infoTickets = ref([]);
    const ticketInfo = ref();
    function setTicketActivo(index) {
      ticketInfo.value = infoTickets.value[index];
      console.log(ticketInfo);
      store.dispatch('Ticket/setActivoAction', index);
    }
    onMounted(() => {
      axios.post('tickets/getTicketsIntervalo').then(({ data }) => {
        const newData = data.map((item) => {
          let dia = new Date(item.timestamp).getDate().toString();
          let mes = (new Date(item.timestamp).getMonth() + 1).toString();
          const year = new Date(item.timestamp).getFullYear();
          const hora = new Date(item.timestamp).getHours();
          const minutos = new Date(item.timestamp).getMinutes();
          if(dia.length < 2) dia = `0${dia}`;
          if(mes.length < 2) mes = `0${mes}`;
          return {
              ...item,
              timestamp: `${dia}/${mes}/${year} ${hora}:${minutos}`,
            };
        });
        const newInfoTickets = data.map((item) => {
          let dia = new Date(item.timestamp).getDate().toString();
          let mes = (new Date(item.timestamp).getMonth() + 1).toString();
          const year = new Date(item.timestamp).getFullYear();
          const hora = new Date(item.timestamp).getHours();
          const minutos = new Date(item.timestamp).getMinutes();
          if(dia.length < 2) dia = `0${dia}`;
          if(mes.length < 2) mes = `0${mes}`;
          console.log(`${dia}/${mes}/${year} ${hora}:${minutos}`);
          return {
            // eslint-disable-next-line no-underscore-dangle
            _id: item._id,
            lista: item.lista,
            total: item.total,
            tipoPago: item.tipoPago,
            fecha: `${dia}/${mes}/${year} ${hora}:${minutos}`,
          };
        });
        infoTickets.value = newInfoTickets;
        listaTickets.value = newData;
        total.value = newData.reduce((t, obj) => obj.total + t, 0);
      });
    });
    return {
      listaTickets,
      setTicketActivo,
      activo,
      total,
      moment,
      ticketInfo,
    };
    /* FINAL SETUP */
  },
  components: {
    AbrirCaja,
    DetalleTicket,
    CerrarCaja,
    SalidaDinero,
    EntradaDinero,
  },
};
</script>
<style scoped>
  .table-responsive {
    height: 80vh;
  }
  .estiloActivoTicketCaja {
    background-color: #ffb5bc85 !important;
  }
  .iconosBootstrap {
    font-size: 2rem;
    text-align: center;
    color: white;
  }
  .botonesWidth {
    width: 5vw;
    border: solid black 1px;
  }
</style>
